              />
              <div class="invalid-feedback">운영시간을 입력해주세요.</div>
            </div>

            <div class="form-group col-md-5">
              <label for="my_number">전화번호</label>
              <input
                type="tel"
                class="form-control"
                name="my_number"
                id="my_pnumber"
                maxlength="13"
                placeholder="전화번호"
                :value="phoneNumber"
                readonly
                disabled
              />
            </div>

            <div class="col-12">
              <label for="address" class="form-label">주소</label>
              <input
                type="text"
                class="form-control"
