                    </button>
                    <button type="button" class="btn btn-primary">
                      Save changes
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
  
  <script>
import B_MenuPluse from "@/components/business/B_MenuPluse.vue";
export default {
  name: "B_ShopRevise",
  components: {
    B_MenuPluse,
  },
  data() {
    return {
      shopName: "", // 상호명 데이터
      category: "", // 분류 데이터
      phoneNumber: "", // 전화번호 데이터
      address: "", // 주소 데이터
      selectedHour: null,
      selectedMinute: null,
      // 새로운 메뉴를 추가하는데 필요한 데이터
      newMenu: {
        name: "", // 메뉴 이름
        description: "", // 메뉴 설명
        price: null, // 메뉴 가격
      },
    };
  },
  created() {
    // 데이터베이스에서 상호명, 분류, 전화번호, 주소를 가져오는 비동기 작업을 수행합니다.
    // 이는 실제 구현에 맞게 수정되어야 합니다.
    this.fetchStoreInfo();
  },
  methods: {
    fetchStoreInfo() {
      // 실제 데이터베이스에서 데이터를 가져와서 할당하는 비동기 작업을 수행합니다.
      // 아래는 가상의 예시입니다.
      setTimeout(() => {
        this.shopName = "가게 이름"; // 데이터베이스에서 가져온 상호명
        this.category = "카테고리"; // 데이터베이스에서 가져온 분류
        this.phoneNumber = "010-1234-5678"; // 데이터베이스에서 가져온 전화번호
        this.address = "서울시 강남구 신사동"; // 데이터베이스에서 가져온 주소
      }, 1000); // setTimeout은 실제 비동기 요청을 대체하기 위한 가상의 예시입니다.
    },
    submitForm() {
      // 폼 제출 시 수행할 동작
      console.log("회원 정보 수정 완료");
      // 여기서 서버에 수정된 정보를 보내거나 다른 작업을 수행할 수 있습니다.
    },
    addMenu() {
      // 새로운 메뉴를 추가하는 메서드
      // 이 곳에서 새로운 메뉴를 처리하는 로직을 구현할 수 있습니다.
      console.log("새로운 메뉴를 추가합니다.");
      console.log(this.newMenu); // 추가된 새로운 메뉴 데이터 출력
      // 여기서 서버에 새로운 메뉴를 추가하는 등의 작업을 수행할 수 있습니다.
    },
  },
};
</script>
  